import React from 'react'
import './Gallery.css'
import Image1 from '../../../images/image1.png'
import Image2 from '../../../images/image2.png'
import Image3 from '../../../images/image4.png'
import Image4 from '../../../images/image5.png'

function Gallery() {
    return (
        <div className="gallery__container">
            <div className="gallery__grid-item">
                <h1 className="gallery__heading">GALLERY</h1>
            </div>

            <div className="gallery__grid-item">
                <img data-aos="zoom-in" data-aos-delay="100" src={Image1} alt="Image" className="gallery__image"/> 
                <img data-aos="zoom-in" data-aos-delay="200" src={Image2} alt="Image" className="gallery__image"/> 
                <img data-aos="zoom-in" data-aos-delay="100" src={Image3} alt="Image" className="gallery__image"/> 
                <img data-aos="zoom-in" data-aos-delay="200" src={Image4} alt="Image" className="gallery__image"/> 
            </div>
        </div>
    )
}

export default Gallery
