import React from 'react'
import './NationalStudies.css'
import axios from 'axios'

export default class NationalStudies extends React.Component {
    // State will apply to the posts object which is set to loading by default
    state = {
      posts: [],
      isLoading: true,
      errors: null
    };

    getPosts() {
      axios
        .get("http://54.255.144.78:3000/tutors?page=0")
        .then(response => {
          this.setState({
            posts: response.data.data,
            isLoading: false
          });
        })
        .catch(error => this.setState({ error, isLoading: false }));
    }

    componentDidMount() {
      this.getPosts();
    }

    render() {
      const { isLoading, posts } = this.state;
      return (
        <React.Fragment>
            <div className="ns__container">
            <h1 className="ns__tutor-head">DTS Panel of Tutor</h1>
                  <div className="ns__post-container" data-aos-delay="200" data-aos="zoom-in-down">
                      {!isLoading ? (
                      posts.map(post => {
                          const { id, name, medium, subject, category, platform, photo_url } = post;
                          return (
                          <div className="ns__post" key={id}>

                              <div className="ns__post">

                                <div className="ns__overlay">
                                    <div className="ns__overlay-text-wrapper">
                                        <div className="ns__subject ns__text">{subject}</div>
                                        <div className="ns__medium ns__text">{medium}</div>
                                        <div className="ns__category ns__text">{category}</div>
                                        <div className="ns__platform ns__text">{platform}</div>
                                    </div>
                                </div>

                                <img src={photo_url} alt="Profile Image" className="ns__profile-image"/>
                                <h3 className="ns__profile-name">{name}</h3>

                              </div>
                          </div>
                          );
                      })
                      ) : 
                      (
                      <p>Loading...</p>
                      )}
                </div>
            </div>
        </React.Fragment>
      );
    }
  }