import React from 'react'
import './Partners.css'
import DtsImage from '../../images/polygon_dts.png'
import OipImage from '../../images/oip.png'
import Partner1Image from '../../images/partner1.png'
import PayhereImage from '../../images/pay-here.png'

function Partners() {
      
    return (
        <div className="partners__container" data-aos-delay="100" data-aos="zoom-in-down">
            <div className="partners__grid-item">
                <img src={OipImage} alt="Partners Image" className="partners__image partners-top"/>
                <img src={Partner1Image} alt="Partners Image" className="partners__image partners-top"/>
            </div>
            <div className="partners__grid-item">
                <img src={OipImage} alt="Partners Image" className="partners__image partners-middle-up"/>
                <img src={DtsImage} alt="Partners Image" className="partners__image partners-middle"/>
                <img src={PayhereImage} alt="Partners Image" className="partners__image partners-middle-down"/>
            </div>
            <div className="partners__grid-item">
                <img src={OipImage} alt="Partners Image" className="partners__image partners-bottom"/>
                <img src={OipImage} alt="Partners Image" className="partners__image partners-bottom"/>
            </div>
        </div>
    )
}

export default Partners
