import React from 'react'
import './TutorUpload.css'

function TutorUpload() {
    return (
        <div className="tutorup__container">
            <form       
                action="http://54.255.144.78:3000/tutor"
                method="post"
                enctype="multipart/form-data"
            >

                <div className="tutorup__grid-item">
                    <h3 className="tutorup__name tutorup__heading">Name</h3>
                    <input className="tutorup__text-input" autocomplete="off"  type="text" name="name" />
                </div>
                <div className="tutorup__grid-item">
                    <h3 className="tutorup__medium tutorup__heading">Medium</h3>
                    <input className="tutorup__text-input" autocomplete="off"  type="text" name="medium" />
                </div>
                <div className="tutorup__grid-item">
                    <h3 className="tutorup__subject tutorup__heading">Subject</h3>
                    <input className="tutorup__text-input" autocomplete="off"  type="text" name="subject" />
                </div>
                <div className="tutorup__grid-item">
                    <h3 className="tutorup__category tutorup__heading">Category</h3>
                    <input className="tutorup__text-input" autocomplete="off"  type="text" name="category" />
                </div>
                <div className="tutorup__grid-item">
                    <h3 className="tutorup__platform tutorup__heading">Platform</h3>
                    <input className="tutorup__text-input" autocomplete="off"  type="text" name="platform" />
                </div>
                <div className="tutorup__grid-item">
                    <h3 className="tutorup__image tutorup__heading">Upload a Image</h3>
                    <input autoComplete="off" className="tutorup__image-input" type="file" name="profile_img" accept="image/*" />
                </div>
                <div className="tutorup__grid-item">
                    <input className="tutorup__submit" type="submit" value="Submit" />
                </div>

            </form>
        </div>
    )
}

export default TutorUpload
