import React from 'react'
import './Home.css'
import "react-responsive-carousel/lib/styles/carousel.min.css";
import { Carousel } from 'react-responsive-carousel';
import YouTube from 'react-youtube';
import { Link } from 'react-router-dom';
import primeTutionOnline from '../../images/primeTutuion-online-logo.png'
import primeTution from '../../images/primeTution-logo.png'
import Hyb2T from '../../images/hyb2t-logo.png'
import image_1 from '../../images/just-image1.jpg'
import image_2 from '../../images/just-image2.jpg'
import image_3 from '../../images/just-image3.jpg'
import facebook from '../../images/facebook.svg'
import youtube from '../../images/youtube.svg'
import twitter from '../../images/twitter.svg'
import instagram from '../../images/instagram.svg'
import { IconContext } from 'react-icons/lib';
import {ChangeLogo} from '../Navbar/Navbar'
import {animateScroll as scroll} from 'react-scroll';

function Home() {

    const opts = {
        playerVars: {
          // https://developers.google.com/youtube/player_parameters
          autoplay: 1,
        },
      };

    const GalleryClick = () => {
        ChangeLogo(false)
        scroll.scrollToTop();
    }

    return (

        <div id='home' className="home__container">
            <div className="home__grid-item">
                <Link to='/link' className="home__image-link">
                    <img src={primeTution} alt="Prime Tution Image" className="home__image" data-aos-delay="100" data-aos="zoom-in-up"/>
                </Link>
                <hr data-aos-delay="100" data-aos="zoom-in-up"/>
                <Link to='/link' className="home__image-link">
                    <img src={Hyb2T} alt="Hyb2T Image" className="home__image" data-aos-delay="100" data-aos="zoom-in-up"/>
                </Link>
                <hr data-aos-delay="100" data-aos="zoom-in-up"/>
                <Link to='/link' className="home__image-link">
                    <img src={primeTutionOnline} alt="Prime Tution Online Image" className="home__image" data-aos-delay="100" data-aos="zoom-in-up"/>
                </Link>
            </div>
            <div className="home__grid-item">

                <div className="home__sub-grid-item">

                    <div className="home__youtube-wrapper" data-aos-delay="100" data-aos="zoom-in-up">
                        <YouTube className="home__youtube" videoId="TsrW3W1y2O0" opts={opts} />
                    </div>
                    <div data-aos-delay="100" data-aos="zoom-in-up">
                        <Link to='/gallery' className="home__carousel-wrapper" onClick={GalleryClick}>
                            <div className="home__carousel-overlay">
                                <div className="overlay-text">GALLERY</div>
                            </div>
                            <Carousel className="home__carousel" showThumbs={false} autoPlay={true} infiniteLoop={true} showArrows={false} showStatus={false}>
                                <div>
                                    <img className="home__carousel-image" src={image_1} />
                                </div>
                                <div>
                                    <img className="home__carousel-image" src={image_2} />
                                </div>
                                <div>
                                    <img className="home__carousel-image" src={image_3} />
                                </div>
                            </Carousel>
                        </Link>
                    </div>

                </div>
                
                <div className="home__sub-grid-item">

                    <ul className="home__social-media-wrapper">

                        <IconContext.Provider value={{color: '#0000ff'}}>
                            <li className="home__social-media">
                                <Link target='__blank' to={'//www.facebook.com'}>
                                    <img src={facebook} alt="Facebook" className="social-media-logo"/>
                                </Link>
                            </li>
                        </IconContext.Provider>

                        <IconContext.Provider value={{color: '#ff0000'}}>
                            <li className="home__social-media">
                                <Link target='__blank' to='//www.youtube.com'>
                                    <img src={youtube} alt="Youtube" className="social-media-logo"/>
                                </Link>
                            </li>
                        </IconContext.Provider>

                        <IconContext.Provider value={{color: '#add8e6'}}>
                            <li className="home__social-media">
                                <Link target='__blank' to='//www.twitter.com'>
                                    <img src={twitter} alt="Twitter" className="social-media-logo"/>
                                </Link>
                            </li>
                        </IconContext.Provider>

                        <IconContext.Provider value={{color: '#ff006a'}}>
                            <li className="home__social-media">
                                <Link target='__blank' to='//www.instagram.com'>
                                    <img src={instagram} alt="Instagram" className="social-media-logo"/>
                                </Link>
                            </li>
                        </IconContext.Provider>

                    </ul>
                    
                </div>

            </div>

        </div>
    )
}

export default Home
