import React from 'react'
import './Tutor.css'
import DtsImage from '../../images/polygon_dts.png'
import BusinessImage from '../../images/polygon_business.png'
import LeadershipImage from '../../images/polygon_leadership.png'
import IeltsImage from '../../images/polygon_ielts.png'
import LanguageImage from '../../images/polygon_languages.png'
import SatImage from '../../images/polygon_sat.png'
import NationalImage from '../../images/polygon_national.png'
import { Link } from 'react-router-dom'

function Tutor() {
      
    return (
        <div className="tutor__container" data-aos-delay="100" data-aos="zoom-in-down">
            <div className="tutor__grid-item">
                <img src={LeadershipImage} alt="tutor Image" className="tutor__image tutor-top"/>
                <img src={BusinessImage} alt="tutor Image" className="tutor__image tutor-top"/>
            </div>
            <div className="tutor__grid-item">
                <Link to='/national-studies'><img src={NationalImage} alt="tutor Image" className="tutor__image tutor-middle-up"/></Link>
                <img src={DtsImage} alt="tutor Image" className="tutor__image  tutor-middle"/>
                <img src={LanguageImage} alt="tutor Image" className="tutor__image tutor-middle-down"/>
            </div>
            <div className="tutor__grid-item">
                <img src={SatImage} alt="tutor Image" className="tutor__image tutor-bottom"/>
                <img src={IeltsImage} alt="tutor Image" className="tutor__image tutor-bottom"/>
            </div>
        </div>
    )
}

export default Tutor
