import React from 'react'
import './Landing.css'
import Logo from '../../images/main-logo.png'


function Landing() {


    return (
        <>
            <div className="landing__container">
                <div className="landing__grid-item">

                    <div className="landing__logo-wrapper">
                        <img src={Logo} alt="Logo" className="landing__logo"/>
                        <h1 className="landing__logo-content">Digital Tutorial Service</h1>
                    </div>
                    
                </div>
                <div className="landing__grid-item">
                    <h1 className="landing__bottom-text">Connecting The Dots</h1>
                    <p className="landing__dot-animation"><span id="dot_1"> . </span><span id="dot_2"> . </span><span id="dot_3"> . </span><span id="dot_4"> . </span><span id="dot_5"> . </span></p>
                </div>
            </div>
        </>
    )
    
}

export default Landing
